
.iconfont, .input_panel--clean {
  font-family: "iconfont" !important;
  font-size: 0.32rem;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.input_panel_wrap {
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #fff;
}
.input_panel_main {
  margin: 0.666667rem 0.866667rem 0;
  padding-bottom: 0.133333rem;
  position: relative;
  border-bottom: 1px solid #333;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.input_panel--input {
  display: block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-right: 0.4rem;
  font-size: 0.533333rem;
}
.input_panel--input > input {
    width: 100%;
    border: none;
    display: block;
    outline: none;
}
.input_panel--clean {
  display: block;
  text-align: center;
  color: #999;
  font-size: 0.4rem;
  width: 0.666667rem;
  line-height: 0.666667rem;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}



