
.iconfont, .list_item_btn span {
  font-family: "iconfont" !important;
  font-size: 0.32rem;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.list_item {
  position: relative;
  padding-bottom: 0.853333rem;
  white-space: nowrap;
  transition-duration: .3s;
  transition-timing-function: ease;
  transition-property: all;
}
.isdelete {
  -webkit-transform: translate(-4rem, 0) !important;
          transform: translate(-4rem, 0) !important;
  transition-duration: .3s;
  opacity: 0;
}
.computed {
  position: relative;
}
.computed .list_item_info {
    color: #999;
}
.computed .list_item_content {
    text-decoration: line-through;
}
.computed .list_item_time {
    position: absolute;
    line-height: 1;
}
.animated {
  transition-duration: .4s;
}
.list_item_infowrap {
  white-space: normal;
  display: inline-block;
  width: 100%;
}
.list_item_info {
  margin: 0 0.853333rem;
  transition: all .3s ease;
}
.list_item_btn {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 1.866667rem;
  display: inline-block;
}
.list_item_time {
  font-size: 0.32rem;
  line-height: 2.5;
}
.list_item_btn span {
  font-size: 0.48rem;
  display: inline-block;
  width: 0.8rem;
  text-align: center;
  line-height: 0.8rem;
}

